<template>
  <div class="screen-orientation-lock">
  </div>
</template>

<script lang="ts">


import {Component, Vue} from "vue-property-decorator";
import StoriesViewer from '~/src/stories_widget/components/Stories/StoriesViewer/StoriesViewer.vue';


@Component({
  name: "ScreenOrientationLock",
})
export default class ScreenOrientationLock extends Vue {
  mounted() {
    // @ts-ignore
    (this.$parent as StoriesViewer).pauseAndBlockViewerUI();
  }

  destroyed() {
    // @ts-ignore
    (this.$parent as StoriesViewer).resumeAndEnableViewerUI();
  }


}

</script>

<style lang="scss">

.screen-orientation-lock {
  pointer-events: all;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  z-index: 1;
  /*background-color: rgba(255,255,255,0.8);*/
  user-select: none;

  background: {
    position: center center;
    size: cover;
    repeat: no-repeat;
    //image: url("screenOrientationLock.png");
    image: url("data:image/png;base64,");
  };


}

