function createApp(StoryManagerProxy) {

  return new Vue({
    store,
    el: '#_app',
    components: {
      StoriesViewer
    },
    // data: function (){
    //   return {
    //     sharedState: store.state,
    //     showStoriesView: this.$store.state.showStoriesView,
    //     storyId: this.$store.state.storyId,
    //     saving: false,
    //     user: this.$store.state.user
    // }
    // },
    data: {

      visible: true,
      showStoriesView: false

    },
    computed: {
      ...mapState('stories', {
        sharedStoryId: (state: StoriesState): number | null => state.storyId,
        sharedShowStoriesView: (state: StoriesState): boolean => state.showStoriesView,
        modeDesktop: (state: StoriesState): boolean => state.modeDesktop
      }),
    },

    watch: {
      sharedShowStoriesView: function (to: boolean, from: boolean): void {

        if (to) {

          this.showStoriesView = true;

          // if ((window as any).WStories) {
          // TODO add temporal backdrop with loader
          // (window as any).WStories.switchToFullScreen();
          // }

          // if (!this.$store.getters['session/id']) {
          //     Session.reInitSession().then(idSession => {});
          // }

          // this.showStoriesView = true;

          try {
            window.focus()
          } catch (e) {
            console.error(e)
          }

        } else {
          this.showStoriesView = false;
          // if ((window as any).WStories) {
          //     (window as any).WStories.switchFromFullScreen();
          // }

          try {
            window.blur()
          } catch (e) {
            console.error(e)
          }
        }


      },
    },
    provide: function () {
      return {
        setVisible: () => {
          this.visible = true
        },
        setHidden: () => {
          this.visible = false
        }
      }
    },
    mounted() {
      this.initHandlers();
    },
    destroyed() {
      // storeUnsubscribe();
      this.removeHandlers();
    },
    methods: {
      initHandlers() {
        window.addEventListener('keydown', this.onKeydown);
      },
      removeHandlers() {
        window.removeEventListener('keydown', this.onKeydown);
      },
      onCloseStoryReader() {
        this.$store.commit('stories/setSharedShowStoriesView', false);
        WStoriesReader.close();
      },
      onKeydown(e) {
        if (e.keyCode === keyCodes.esc) {
          // this.onCloseStoryReader();
          try {
            this.$refs['storiesViewer']?.closeViewer();
          } catch (e) {
            console.error(e);
          }

        }
      },
    },
    render(h: CreateElement): VNode | undefined {
      if (this.showStoriesView) {
        return h('StoriesViewer', {
          ref: 'storiesViewer',
          props: {
            id: this.sharedStoryId,
            timeout: 10000,
            timerEnabled: true,
            storyManagerProxy: (name, data) => StoryManagerProxy.rpcCallEventWithCb(name, data),
          },
          style: {display: this.visible ? 'block' : 'none'},
          on: {
            close: this.onCloseStoryReader,
            showSlide: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'showSlide', {id: event.id, index: event.index});
            },
            showStory: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'showStory', {id: event.id});
            },
            closeStory: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'closeStory', {id: event.id});
            },
            closeStoryByScrolling: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'closeStoryByScrolling', {id: event.id});
            },
            clickOnButton: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'clickOnButton', {
                id: event.id,
                index: event.index,
                url: event.url,
                elementId: event.elementId,
              });
            },
            clickOnSwipeUpGoods: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'clickOnSwipeUpGoods', {
                id: event.id,
                index: event.index,
                url: event.url,
                elementId: event.elementId,
                modeDesktop: event.modeDesktop,
                width: event.width,
                height: event.height,
                verticalMargin: event.verticalMargin,
              });
            },
            likeStory: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'likeStory', event);
            },
            dislikeStory: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'dislikeStory', event);
            },
            favoriteStory: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'favoriteStory', event);
            },
            favoriteStoryInternal: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'favoriteStoryInternal', event);
            },
            shareStory: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'shareStory', event);
            },
            shareStoryWithPath: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'shareStoryWithPath', event);
            },
            storyOpenedInternal: (event) => {
              (window as any).cur?.Rpc?.callMethod('event', 'storyOpenedInternal', event);
            },
            openViewer: (payload: { storiesId: number }) => WStoriesReader.setFixedHtmlPosition(),
          }
        })
      } else {
        return undefined;
      }
    }
  })
}


function mount(options: IWidgetStoriesOptions, StoryManagerProxy) {

  // sync
  store.commit('stories/setOptions', options);

  // Session.setStoreLink(store);

  if ((window as any)['app']) {
    (window as any)['app_'] = createApp(StoryManagerProxy)
  }

}

