







import {Component, Vue} from "vue-property-decorator";
import StoriesViewer from '~/src/stories_widget/components/Stories/StoriesViewer/StoriesViewer.vue';


@Component({
  name: "ScreenOrientationLock",
})
export default class ScreenOrientationLock extends Vue {
  mounted() {
    // @ts-ignore
    (this.$parent as StoriesViewer).pauseAndBlockViewerUI();
  }

  destroyed() {
    // @ts-ignore
    (this.$parent as StoriesViewer).resumeAndEnableViewerUI();
  }


}

