      try {
        (window as any)._narrative_quiz.initWidget(widget);
        (this.$store as any).dispatch('stories/SET_ACTIVE_ELEMENT_QUIZ', widget)
      } catch (e) {
      }
    } else {
      (this.$store as any).dispatch('stories/SET_ACTIVE_ELEMENT_QUIZ', undefined)
    }
    return false;
  }

